<template>
  <div>
    <el-card class="box-card">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>广告管理</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="box_border">
        <div class="group">
          <h4>添加广告</h4>
          <el-divider />
          <el-form ref="form" :rules="rules" :model="form" label-width="80px" label-position="left">
            <el-form-item label="名称" prop="name">
              <el-input v-model="form.name" />
            </el-form-item>
            <el-form-item label="* 图片" prop="imageUrl">
              <el-upload class="avatar-uploader" action="http-request" :http-request="uploadSectionFile" :show-file-list="false">
                <el-image v-if="form.imageUrl" :src="form.imageUrl" fit="contain" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon" />
              </el-upload>
              <p style="color: #999999; font-size: 10px">只能上传jpg/png文件，且不超过10MB，最多可上传5张</p>
            </el-form-item>
            <el-form-item label="链接" prop="advertisingUrl">
              <el-input v-model="form.advertisingUrl" />
            </el-form-item>
            <el-form-item label="排序" prop="sn">
              <el-input v-model.number="form.sn" />
            </el-form-item>
            <el-form-item size="medium">
              <el-button class="inquire" style="width: 100px" type="success" round @click="submitForm('form')">保存</el-button>
              <router-link :to="{ path: '/adList' }"><el-button style="width: 100px" round>取消</el-button></router-link>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'AdAdd',

  data() {
    return {
      adId: '', // 编辑时获取的广告id
      form: {
        name: '', // 图片名称
        imageUrl: '', // 编辑时回显的图片
        advertisingUrl: '', // 链接地址
        sn: '' // 序号
      },
      rules: {
        name: [
          { required: true, message: '请输入图片名称', trigger: 'blur' }
        ],
        advertisingUrl: [
          { required: true, message: '请输入链接地址', trigger: 'change' }
        ],
        sn: [
          { required: true, message: '请输入序号', trigger: 'blur' },
          { type: 'number', message: '序号必须为数字' }
        ]
      }
    }
  },

  methods: {
    // 自定义覆盖图片上传方法
    uploadSectionFile(params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG/PNG 格式!')
        return
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 10MB!')
        return
      }
      const formData = new FormData()
      formData.append('file', params.file)
      this._upload(formData)
    },
    // 新增时提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (!this.form.imageUrl) {
            this.$message.error('请上传图片')
            return false
          }
          if (this.adId) {
            // 编辑
            this._updateAd()
              .then((res) => {
                if (res) {
                  this.$message.success('保存成功')
                  this.$router.push('/adList')
                }
              })
          } else {
            // 新增
            this._addAdItem()
              .then((res) => {
                if (res) {
                  this.$message.success('保存成功')
                  this.$router.push('/adList')
                }
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 接口api----------------------------------------------------------
    // 添加广告
    _addAdItem() {
      let data = this.form
      data.enableStatus = 0
      return this.$http.post('/woho/shop/advertising/insert', data)
        .then((res) => {
          return true
        })
    },
    // 上传图片
    _upload(formData) {
      console.log('formData', formData)

      this.$http.post('/common/oss/uploadFile', formData)
        .then((res) => {
          console.log(res.data.data.webUrl)
          this.form.imageUrl = res.data.data.webUrl
        })
    },
    // 根据id查询
    _findAdData () {
      this.$http.post('/woho/shop/advertising/findById/' + this.adId)
        .then((res) => {
          this.form = res.data
        })
    },
    // 编辑店铺广告
    _updateAd() {
      return this.$http.put('/woho/shop/advertising/update', this.form)
        .then((res) => {
          return true
        })
    }
  },

  created() {
    this.adId = this.$route.query.id
    if (this.adId) {
      // 编辑时的回显
      this._findAdData()
    }
  },
}
</script>

<style lang="scss" scoped>
  .box-card{
    min-width: 1100px;
    margin-top:15px;
    .box_border{
      margin: 70px auto;
      width: 740px;
      border-radius: 10px;
      border: solid 1px #bfbfbf;
      .group{
        width: 586px;
        margin: auto;
        .avatar-uploader .el-upload .el-icon-plus{
          border: 1px dashed #d9d9d9 !important;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          // overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
          border-color: #409EFF;
        }
        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 178px;
          height: 178px;
          line-height: 178px;
          text-align: center;
        }
        .avatar {
          width: 178px;
          height: 178px;
          display: block;
        }
      }
    }
  }
</style>
